<template>
	<view class="table_box">
		<view class="tabelTh">
			<text :class="status!=14 && status!=5 ?'hasIndex':''" v-for="(item,index) in thList" :key="index">{{item.name}}</text>
		</view>
		<view v-if="tdList.length > 0">
			<view class="tabelTr" v-for="(item,index) in tdList" :key="index">
				<text class="oneIndex" v-if="status!=14 && status!=5">{{index + 1}}</text>
				<text v-if="status!=5 && status!=6">{{item.projectName}}</text>
				<!-- 成交-金额 -->
				<text v-if="status==7">{{item.transactionAmount}}</text>
				<!-- 成交-套数 -->
				<text v-if="status==8">{{item.transactionCount}}</text> 
				<!-- 成交-面积 -->
				<template v-if="status==1">
					<text>{{item.soldArea}}</text>
					<text>{{item.surplusArea}}</text>
				</template>
				<!-- 成交-均价 -->
				<text v-if="status==9">{{item.averagePrice}}</text>
				 <!-- 回款-总回款 -->
				<text v-if="status==10">{{item.refundsAmount}}</text> 
				<!-- 回款-按揭款 -->
				<text v-if="status==11">{{item.mortgageAmount}}</text> 
				<!-- 房源-套数 -->
				<template v-if="status==2">
					<text>{{item.roomCount}}</text>
					<text>{{item.countRemovalRate}}</text>
					<text>{{item.surplusRoomCount}}</text>
				</template>
				<!-- 房源-面积 -->
				<template v-if="status==12">
					<text>{{item.totalArea}}</text>
					<text>{{item.areaRemovalRate}}</text>
					<text>{{item.surplusArea}}</text>
				</template>
				<!-- 房源-营收 -->
				<template v-if="status==15">
					<text>{{item.totalRoomMoney}}</text>
					<text>{{item.moneyRemovalRate}}</text>
					<text>{{item.unSoldRoomMoney}}</text>
				</template>
				<!-- 来访来电-次数 -->
				<text v-if="status==3">{{item.visitorAmount}}</text>
				<!-- 转化率-认购 -->
				<template v-if="status==4">
					<text>{{item.visitorAmount}}</text>
					<text>{{item.subscribeAmount}}</text>
					<text>{{item.subscribeRate}}</text>
				</template>
				<!-- 转化率-签约 -->
				<template v-if="status==13">
					<text>{{item.subscribeAmount}}</text>
					<text>{{item.signAmount}}</text>
					<text>{{item.signingRate}}</text>
				</template>
				<!-- 逾期情况 -->
				<template v-if="status==14">
					<text>{{item.followUpOverdue}}</text>
					<text>{{item.signOverdue}}</text>
					<text>{{item.subscribeOverdue}}</text>
					<text>{{item.totalOverdue}}</text>
				</template>
				<!-- 案场统计-业绩排名 -->
				<template v-if="status==5">
					<text>{{item.rank}}</text>
					<text>{{item.salesmanName}}</text>
					<text>{{item.count}}</text>
					<text>{{item.money}}</text>
				</template>
				<!-- 案场统计-转化情况 -->
				<template v-if="status==6">
					<text>{{item.salesmanName}}</text>
					<text>{{type=='rate'?item.transRate:item.transCycle}}</text>
					<text>{{item.compareToAvg}}</text>
				</template>
			</view>
		</view>
		<view class="noDataText" v-else>暂无数据</view>
	</view>
</template>

<script>
	export default {
		props:['status','thList','tdList','type'],
		data() {
			return {}
		},
		mounted(){
			
		}
	}
</script>

<style lang="scss">
	.table_box{
		background: #fff;
		border-radius: 8rpx;
		border: 1rpx solid #e5e5e5;
		.tabelTh{
			padding: 24rpx 0;
			color: #fff;
			font-weight: bold;
			border-bottom: 1rpx solid #eee;
			display: flex;
			position: sticky;
			top: 170rpx;
			background: #03958C;
			font-size: 24rpx;
			text{
				text-align: center;
				flex: 1;
			}
			.hasIndex:first-child{
				// max-width: 60rpx;
			}
			// text:first-child{
			// 	max-width: 60rpx;
			// }
		}
		.noDataText{
			font-size: 28rpx;
			text-align: center;
			padding: 30rpx 0;
			border-bottom: 1rpx solid #eee;
		}
		.tabelTr{
			padding: 24rpx 0;
			color: #333;
			display: flex;
			text{
				text-align: center;
				flex: 1;
			}
			.oneIndex{
				// max-width: 60rpx;
			}
		}
		.tabelTr:nth-child(2n){
			background-color: #e8f1f0!important;
		}
	}
	.xuNumber{
		width: 20rpx;
	}
</style>
